import { Add, Remove } from "@mui/icons-material";
import React, { useState } from "react";
import styled from "styled-components";
import Announcement from "../Components/Announcement";
import Footer from "../Components/Footer";
import Navbar from "../Components/Navbar";
import Newsletter from "../Components/Newsletter";
import { mobile } from "../responsive";
import { useEffect } from "react";
import { useHistory, useLocation } from "react-router-dom";
import { publicRequest } from "../requestMethod";
import { useDispatch, useSelector } from "react-redux";
import { createCart, updateCart } from "../redux/apiCalls";

function Product() {
  const location = useLocation();
  const history = useHistory();
  const id = location.pathname.split("/")[2];
  const [product, setProduct] = useState({});
  const [quantit, setQuantity] = useState(1);
  const dispatch = useDispatch();
  const cartData = useSelector((state) => state.cart);
  const user = useSelector((state) => state.user.currentUser);
  const [color, setColor] = useState("");
  const [size, setSize] = useState("");

  const getProduct = async () => {
    try {
      const res = await publicRequest.get(`/product/find/${id}`);
      setProduct(res.data);
    } catch (error) {
      console.log(error);
    }
  };

  const createUpdatedCart = () => {
    const { _id, title, price, img, ...other } = { ...product };
    const newProduct = {
      _id,
      title,
      price,
      img,
      quantity: quantit,
      color,
      size,
    };
    const data = {
      products: [...cartData.products, newProduct],
      quantity: parseInt(cartData.quantity) + 1,
      total: cartData.total + parseFloat(price) * parseFloat(quantit),
    };
    return data;
  };

  const addToCartinServer = () => {
    if (user) {
      try {
        const data = createUpdatedCart();
        if (!cartData.products.length) {
          dispatch(createCart(user?._id, data));
        } else {
          dispatch(updateCart(user?._id, data));
        }
      } catch (error) {
        console.log(error);
      }
    } else {
      history.push("/login");
    }
  };

  useEffect(() => {
    getProduct();
  }, [id]);
  useEffect(() => {
    if (product?.size) {
      setSize(product?.size[0]);
      setColor(product?.color[0]);
    }
  }, [product]);

  return (
    <Container>
      <Navbar />
      <Announcement />
      <Wrapper>
        <ImageContainer>
          <Image src={product.img}></Image>
        </ImageContainer>
        <InfoContainer>
          <Title>{product.title}</Title>
          <Desc>{product.description}</Desc>
          <Price>PKR {product.price}</Price>
          <FilterContainer>
            <Filter>
              <FilterTitle>Color</FilterTitle>
              {product.color?.map((c, i) => (
                <FilterColor onClick={() => setColor(c)} key={i} color={c} />
              ))}
            </Filter>
            <Filter>
              <FilterTitle>Size</FilterTitle>
              <FilterSize onChange={(e) => setSize(e.target.value)}>
                {product.size?.map((c, i) => (
                  <FilterSizeOption key={i}>{c}</FilterSizeOption>
                ))}
              </FilterSize>
            </Filter>
          </FilterContainer>
          <AddContainer>
            <AmountContainer>
              <Remove
                onClick={() => setQuantity(quantit > 1 ? quantit - 1 : quantit)}
                style={{ cursor: "pointer" }}
              />
              <Ammount>{quantit}</Ammount>
              <Add
                onClick={() => setQuantity(quantit + 1)}
                style={{ cursor: "pointer" }}
              />
            </AmountContainer>
            <Button onClick={addToCartinServer}>ADD TO CART</Button>
          </AddContainer>
        </InfoContainer>
      </Wrapper>
      <Newsletter />
      <Footer />
    </Container>
  );
}

export default Product;
const Container = styled.div`
  width: 100vw;
`;
const Wrapper = styled.div`
  padding: 5rem;
  display: flex;
  ${mobile({
    padding: "3rem 1rem",
    flexDirection: "column",
  })}
`;
const ImageContainer = styled.div`
  flex: 1;
  height: 70vh;
`;
const Image = styled.img`
  width: 100%;
  height: 90%;
  object-fit: cover;
  ${mobile({
    height: "40vh",
  })}
`;
const InfoContainer = styled.div`
  flex: 1;
  padding: 0rem 5rem;
  ${mobile({
    padding: "2rem 1rem",
  })}
`;
const Title = styled.h1`
  font-weight: 300;
  font-size: 4rem;
`;
const Desc = styled.p`
  font-size: 1.8rem;
  margin: 2rem 0rem;
  ${mobile({
    margin: "1rem 0rem",
    fontSize: "1.8rem",
  })}
`;
const Price = styled.span`
  font-weight: 10;
  font-size: 3.5rem;
  ${mobile({
    fontSize: "3.5rem",
  })}
`;
const FilterContainer = styled.div`
  display: flex;
  justify-content: space-between;
  width: 60%;
  margin: 2.5rem 0rem;
  ${mobile({
    width: "100%",
  })}
`;
const Filter = styled.div`
  display: flex;
  align-items: center;
`;
const FilterTitle = styled.h1`
  font-weight: 200;
  font-size: 2rem;
  ${mobile({
    fontSize: "2.5rem",
  })}
`;
const FilterColor = styled.div`
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background-color: ${(props) => props.color};
  margin: 0rem 0.5rem;
  cursor: pointer;
`;
const FilterSize = styled.select`
  margin-left: 1rem;
  padding: 0.5rem;
`;
const FilterSizeOption = styled.option``;
const AddContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 60%;
  ${mobile({
    width: "100%",
  })}
`;
const AmountContainer = styled.div`
  display: flex;
  align-items: center;
`;
const Ammount = styled.span`
  width: 3rem;
  height: 3rem;
  display: flex;
  border-radius: 1rem;
  border: 1px solid teal;
  align-items: center;
  justify-content: center;
  margin: 0rem 0.5rem;
  font-size: 1.6rem;
  font-weight: 500;
  ${mobile({
    fontSize: "2rem",
  })};
`;
const Button = styled.button`
  padding: 0.8rem;
  border: 0.2rem solid teal;
  background-color: white;
  font-weight: 700;
  cursor: pointer;
  font-size: 1.6rem;
  &:hover {
    background-color: #f8f4f4;
  }
  ${mobile({
    padding: "1rem",
  })}
`;
