import { publicRequest, userRequest } from "../requestMethod";
import {
  addProduct,
  emptyCart,
  loginfaliure,
  loginstart,
  loginsuccess,
} from "./Action";

export const login = (user) => {
  return async (dispatch) => {
    dispatch(loginstart());
    try {
      const res = await publicRequest.post("/auth/login", user);
      dispatch(loginsuccess(res.data));
      dispatch(loadUserCart(res.data._id));
    } catch (error) {
      dispatch(loginfaliure());
    }
  };
};

export const updateCart = (userId, data) => {
  return async (dispatch) => {
    try {
      const res = await userRequest.put(`/cart/${userId}`, {
        userId,
        ...data,
      });

      res.status === 200 && dispatch(addProduct(data));
    } catch (error) {
      console.log(error);
    }
  };
};

export const createCart = (userId, data) => {
  return async (dispatch) => {
    try {
      const res = await userRequest.post("/cart", {
        userId,
        ...data,
      });
      res.status === 200 && dispatch(addProduct(data));
    } catch (error) {
      console.log(error);
    }
  };
};

export const deleteCart = (id) => {
  return async (dispatch) => {
    const res = await userRequest.delete(`/cart/${id}`);
    res.status === 200 && dispatch(emptyCart());
  };
};

export const loadUserCart = (id, data) => {
  return async (dispatch) => {
    try {
      const res = await userRequest.get(`/cart/find/${id}`);
      if (res.status === 200) {
        const { products, quantity, total, ...rest } = { ...res.data };
        if (products.length)
          dispatch(
            addProduct({
              products,
              quantity,
              total,
            })
          );
      }
    } catch (error) {
      console.log(error);
    }
  };
};
