import React, { useEffect } from "react";
import styled from "styled-components";
import { Search, ShoppingCartOutlined } from "@mui/icons-material";
import { Badge } from "@mui/material";
import { mobile } from "../responsive";
import { Link, Redirect } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { logout } from "../redux/Action.js";
import { useState } from "react";

function Navbar(props) {
  const dispatch = useDispatch();
  const store = useSelector((state) => state.cart);
  const login = useSelector((state) => state.user);
  const [search, setSearch] = useState("");
  const quantity = store.quantity;

  return (
    <Container>
      <Wrapper>
        <Left>
          <Language>En</Language>
          <SearchContainer>
            <Input
              placeholder="Search"
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            <Search style={{ color: "gray", fontSize: "1.6rem" }} />
          </SearchContainer>
        </Left>
        <Center>
          <Link to="/">
            <Logo>SHopBY</Logo>
          </Link>
        </Center>
        <Right>
          {!login.currentUser && (
            <Link to="/register">
              <MenuItem>REGISTER</MenuItem>
            </Link>
          )}
          {login.currentUser ? (
            <MenuItem
              onClick={() => {
                dispatch(logout());
              }}
            >
              LOGOUT
            </MenuItem>
          ) : (
            <Link to="/login">
              <MenuItem>SIGN&nbsp;IN</MenuItem>
            </Link>
          )}
          <Link to="/cart">
            <MenuItem>
              <Badge badgeContent={quantity} color="primary">
                <ShoppingCartOutlined style={{ fontSize: "2.5rem" }} />
              </Badge>
            </MenuItem>
          </Link>
        </Right>
      </Wrapper>
    </Container>
  );
}

export default Navbar;

const Container = styled.div`
  height: 6rem;
  ${mobile({
    height: "5rem",
  })}
`;
const Wrapper = styled.div`
  padding: 1rem 3rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  ${mobile({
    padding: "1rem 0rem",
  })}
`;
const Left = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
`;

const Language = styled.span`
  font-size: 1.4rem;
  cursor: pointer;
  ${mobile({
    display: "none",
  })}
`;
const SearchContainer = styled.div`
  border: 0.05rem solid lightgray;
  display: flex;
  align-items: center;
  margin-left: 2.5rem;
  padding: 0.5rem;
  ${mobile({
    marginLeft: "2.2rem",
    width: "8rem",
  })}
`;
const Input = styled.input`
  border: none;
  outline: none;
  ${mobile({
    width: "5rem",
    fontSize: "1rem",
  })}
`;
const Center = styled.div`
  flex: 1;
  text-align: center;
`;
const Logo = styled.h1`
  font-weight: bold;
  font-size: 2.5rem;
  ${mobile({
    fontSize: "1.8rem",
  })}
  color: black
`;
const Right = styled.div`
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  ${mobile({
    justifyContent: "center",
    flex: 1.5,
  })}
`;
const MenuItem = styled.div`
  font-size: 1.4rem;
  cursor: pointer;
  margin-left: 2.5rem;
  .css-106c1u2-MuiBadge-badge {
    font-size: 1.2rem;
  }
  color: black;
  ${mobile({
    fontSize: "1.2rem",
    marginLeft: "1.2rem",
  })}
`;
