import { LOGINFALIURE, LOGINSTART, LOGINSUCCESS, LOGOUT } from "./ActionTypes";

const initialState = {
  currentUser: null,
  isFetching: false,
  error: false,
};

const LoginReducer = (state = initialState, action) => {
  switch (action.type) {
    case LOGINSTART:
      return { ...initialState, isFetching: true };
    case LOGINSUCCESS:
      return {
        ...initialState,
        currentUser: action.payload,
      };
    case LOGINFALIURE:
      return { ...initialState, error: true };
    case LOGOUT:
      return initialState;
    default:
      return state;
  }
};

export default LoginReducer;
