import {
  ADDPRODUCT,
  EMPTYCART,
  LOGINFALIURE,
  LOGINSTART,
  LOGINSUCCESS,
  LOGOUT,
  REMOVEPRODUCT,
} from "./ActionTypes";

export const addProduct = (items) => {
  return {
    type: ADDPRODUCT,
    payload: items,
  };
};

export const removeProduct = () => {
  return {
    type: REMOVEPRODUCT,
  };
};

export const loginsuccess = (data) => {
  return {
    type: LOGINSUCCESS,
    payload: data,
  };
};
export const loginstart = () => {
  return {
    type: LOGINSTART,
  };
};
export const loginfaliure = () => {
  return {
    type: LOGINFALIURE,
  };
};
export const logout = () => {
  return {
    type: LOGOUT,
  };
};

export const emptyCart = () => {
  return {
    type: EMPTYCART,
  };
};
