import { Add, Remove } from "@mui/icons-material";
import React from "react";
import styled from "styled-components";
import Announcement from "../Components/Announcement";
import Footer from "../Components/Footer";
import Navbar from "../Components/Navbar";
import { mobile } from "../responsive";
import { useSelector, useDispatch } from "react-redux";
import { deleteCart, updateCart } from "../redux/apiCalls";

function Cart(props) {
  const userId = useSelector((state) => state.user.currentUser?._id);
  const cart = useSelector((state) => state.cart);
  const dispatch = useDispatch();

  const manipulatingProducts = (product, action) => {
    const products = [...cart.products];
    const index = products.indexOf(product);
    if (product.quantity === 1 && action === "Sub") {
      const updatedProducts = products.filter((p, i) => i !== index);
      if (updatedProducts.length) {
        const updatedCart = {
          products: updatedProducts,
          quantity: cart.quantity - 1,
          total: cart.total - product.price,
        };
        dispatch(updateCart(userId, updatedCart));
      } else {
        dispatch(deleteCart(userId));
      }
    } else if (action === "Sub") {
      const quantity = products[index].quantity - 1;
      products[index] = { ...products[index], quantity };
      const updatedCart = {
        products: products,
        quantity: cart.quantity,
        total: cart.total - product.price,
      };
      dispatch(updateCart(userId, updatedCart));
    } else if (action === "Add") {
      const quantity = products[index].quantity + 1;
      products[index] = { ...products[index], quantity };
      const updatedCart = {
        products: products,
        quantity: cart.quantity,
        total: cart.total + parseInt(product.price),
      };
      dispatch(updateCart(userId, updatedCart));
    }
  };

  return (
    <Container>
      <Navbar />
      <Announcement />
      <Wrapper>
        <Title>Your Cart</Title>

        <Top>
          <Topbutton>Continue Shopping</Topbutton>
          <TopTexts>
            <TopText>Shopping Bag({cart.quantity})</TopText>
            <TopText>Your wishlist (6)</TopText>
          </TopTexts>
          <Topbutton onClick={() => dispatch(deleteCart(userId))} type="Filled">
            Empty Cart
          </Topbutton>
        </Top>
        <Bottom>
          <Info>
            {cart.products.map((c, i) => (
              <Product key={i}>
                <ProductDetail>
                  <Image src={c.img} />
                  <Detail>
                    <PrdouctName>
                      <b>Product:</b> {c.title}
                    </PrdouctName>
                    <PrdouctId>
                      <b>ID:</b> {c._id}
                    </PrdouctId>
                    <PrdouctColor color={c.color} />
                    <PrdouctSize>
                      <b>Size:</b> {c.size}
                    </PrdouctSize>
                  </Detail>
                </ProductDetail>
                <PriceDetail>
                  <ProductAmountContainer>
                    <Add
                      style={{ cursor: "pointer" }}
                      onClick={() => {
                        manipulatingProducts(c, "Add");
                      }}
                    />
                    <ProductAmount>{parseInt(c.quantity)}</ProductAmount>
                    <Remove
                      style={{ cursor: "pointer" }}
                      onClick={() => {
                        manipulatingProducts(c, "Sub");
                      }}
                    />
                  </ProductAmountContainer>
                  <ProductPrice>
                    PKR&nbsp;{parseInt(c.price) * parseInt(c.quantity)}
                  </ProductPrice>
                </PriceDetail>
                <Hr />
              </Product>
            ))}
          </Info>
          <Summary>
            <SummaryTitle>ORDER Summary</SummaryTitle>
            <SummaryItem>
              <SummaryItemText>Subtotal</SummaryItemText>
              <SummaryItemPrice>PKR {cart.total}</SummaryItemPrice>
            </SummaryItem>
            <SummaryItem>
              <SummaryItemText>Estimated Shipping</SummaryItemText>
              <SummaryItemPrice>PKR 50.90</SummaryItemPrice>
            </SummaryItem>
            <SummaryItem>
              <SummaryItemText>Sipping Discount</SummaryItemText>
              <SummaryItemPrice>PKR -50.90</SummaryItemPrice>
            </SummaryItem>
            <SummaryItem type="total">
              <SummaryItemText>Total</SummaryItemText>
              <SummaryItemPrice>PKR {cart.total}</SummaryItemPrice>
            </SummaryItem>
            <Button>CheckOut now</Button>
          </Summary>
        </Bottom>
      </Wrapper>
      <Footer />
    </Container>
  );
}

export default Cart;
const Container = styled.div`
  width: 100vw;
`;
const Wrapper = styled.div``;
const Title = styled.h1`
  text-align: center;
  font-size: 3rem;
  font-weight: 300;
  margin-top: 1rem;
  text-transform: uppercase;
`;
const Top = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 2rem;
`;
const Topbutton = styled.button`
  font-weight: 600;
  cursor: pointer;
  text-transform: uppercase;
  background-color: ${(props) =>
    props.type === "Filled" ? "black" : "transparent"};
  border: ${(props) => props.type === "Filled" && "none"};
  color: ${(props) => props.type === "Filled" && "White"};
  padding: ${(props) => (props.type === "Filled" ? 1.5 : 1)}rem;
  :hover {
    opacity: 0.8;
  }
`;

const TopTexts = styled.div`
  ${mobile({
    display: "none",
  })}
`;
const TopText = styled.span`
  cursor: pointer;
  text-decoration: underline;
  font-size: 1.4rem;
  margin: 0rem 1rem;
`;
const Bottom = styled.div`
  display: flex;
  justify-content: space-between;
  padding: 0rem 2rem;
  font-size: 1.2rem;
  ${mobile({
    flexDirection: " column",
  })}
`;
const Info = styled.div`
  flex: 3;
`;
const Product = styled.div`
  display: flex;
  justify-content: space-between;
  ${mobile({
    flexDirection: "column",
  })}
`;
const ProductDetail = styled.div`
  display: flex;
  justify-content: center;
  flex: 2;
`;
const Image = styled.img`
  width: 20rem;
`;
const Detail = styled.div`
  flex: 2;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
`;
const PrdouctName = styled.span``;
const PrdouctId = styled.span``;
const PrdouctColor = styled.div`
  background: ${(props) => props.color};
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
`;
const PrdouctSize = styled.span``;
const PriceDetail = styled.div`
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;
const ProductAmountContainer = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
`;
const ProductAmount = styled.span`
  font-size: 2rem;
  margin: 0rem 0.5rem;
  ${mobile({
    margin: ".5rem 2rem",
    fontSize: "3rem",
  })}
`;
const ProductPrice = styled.span`
  font-size: 2.5rem;
  margin: 1.5rem 0rem;
  font-weight: 300;
  ${mobile({
    fontSize: "3rem",
  })}
`;
const Hr = styled.hr`
  height: 0.2rem;
  background-color: #eee;
  border: none;
`;
const Summary = styled.div`
  flex: 1;
  height: 50vh;
  border: 0.1rem solid lightgray;
  padding: 1rem 1.2rem;
  display: flex;
  flex-direction: column;
`;
const SummaryTitle = styled.h2`
  text-transform: uppercase;
  font-size: 2.2rem;
  font-weight: 300;
  margin-bottom: 1rem;
`;
const SummaryItem = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 0rem;
  font-size: ${(props) => props.type === "total" && 2}rem;
  font-weight: ${(props) => props.type === "total" && 500};
`;
const SummaryItemText = styled.span``;
const SummaryItemPrice = styled.span``;
const Button = styled.button`
  color: white;
  background: black;
  border: none;
  padding: 1rem;
  text-transform: uppercase;
  cursor: pointer;
  font-weight: 600;
  margin-top: 1em;
  :hover {
    opacity: 0.8;
  }
`;
