import { ADDPRODUCT, EMPTYCART, LOGOUT, REMOVEPRODUCT } from "./ActionTypes";

const initialState = {
  products: [],
  quantity: 0,
  total: 0,
};

const CartReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADDPRODUCT:
      return action.payload;
    case REMOVEPRODUCT:
      return { ...state, quantity: state.quantity - 1 };
    case EMPTYCART:
      return initialState;
    case LOGOUT:
      return initialState;
    default:
      return state;
  }
};

export default CartReducer;
