import { ArrowLeftOutlined, ArrowRightOutlined } from "@mui/icons-material";
import React, { useState } from "react";
import styled from "styled-components";
import { Data } from "../data";
import { mobile } from "../responsive";

function Slider(props) {
  const [slideIndex, setSlideIndex] = useState(0);
  const handleClick = (direction) => {
    if (direction === "left") {
      setSlideIndex(slideIndex > 0 ? slideIndex - 1 : Data.length - 1);
    } else {
      setSlideIndex(slideIndex === Data.length - 1 ? 0 : slideIndex + 1);
    }
  };
  return (
    <Container>
      <Arrow direction="left" onClick={() => handleClick("left")}>
        <ArrowLeftOutlined style={{ fontSize: "2.5rem" }} />
      </Arrow>
      <Wrapper transX={slideIndex}>
        {Data.map((item) => {
          return (
            <Slide key={item.id} bg={item.bg}>
              <ImgContainer>
                <Image src={item.img} alt="" />
              </ImgContainer>
              <InfoContainer>
                <Title>{item.title}</Title>
                <Description>{item.description}</Description>
                <Button>SHOP NOW</Button>
              </InfoContainer>
            </Slide>
          );
        })}
      </Wrapper>
      <Arrow direction="right" onClick={() => handleClick("right")}>
        <ArrowRightOutlined style={{ fontSize: "2.5rem" }} />
      </Arrow>
    </Container>
  );
}

export default Slider;
const Container = styled.div`
  width: 100%;
  height: 100vh;
  display: flex;
  position: relative;
  overflow: hidden;
  ${mobile({
    display: "none",
  })}
`;
const Arrow = styled.div`
  width: 5rem;
  height: 5rem;
  background: #fff7f7;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: ${(props) => props.direction === "left" && "1.5rem"};
  right: ${(props) => props.direction === "right" && "1.5rem"};
  margin: auto;
  cursor: pointer;
  opacity: 0.5;
  z-index: 2;
`;
const Wrapper = styled.div`
  height: 100%;
  display: flex;
  transform: translateX(${(props) => props.transX * -100}vw);
  transition: 0.5s linear all;
`;

const Slide = styled.div`
  display: flex;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #${(props) => props.bg};
`;
const ImgContainer = styled.div`
  flex: 1;
  height: 100%;
  align-items: center;
  display: flex;
  flex-direction: column;
`;
const Image = styled.img`
  height: 80%;
  width: 100%;
  object-fit: contain;
`;
const InfoContainer = styled.div`
  flex: 1;
  padding: 5rem;
  animation: back 1s;
  animation-iteration-count: 1;
  @keyframes back {
    0% {
      transform: translateX(10vw);
      opacity: 0.5;
    }
    100% {
      transform: translateX(0vw);
    }
  }
`;
const Title = styled.h1`
  font-size: 7rem;
`;
const Description = styled.p`
  margin: 5rem 0;
  font-size: 2rem;
  font-weight: 500;
  letter-spacing: 0.3rem;
`;

const Button = styled.button`
  padding: 1rem;
  font-size: 2rem;
  background-color: transparent;
  cursor: pointer;
`;
